.header {
  background: #242440;
  box-shadow: 0px 4px 15px #000000;
  padding: 18px 0;
  .container {
    max-width: 1820px;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    display: flex;
    align-items: center;
  }
  &__right {
    display: flex;
    align-items: center;
  }
  &__logo {
    display: block;
    width: 110px;
    img {
      width: 100%;
      height: auto;
    }
  }
  &__play {
    font-family: 'Neue Einstellung';
    font-weight: 700;
    font-size: 18px;
    line-height: 120%;
    color: #FFFFFF;
    margin-left: 47px;
  }
  &__item {
    position: relative;
    margin-left: 15px;
   &--active {
     .header__item-hidden {
       opacity: 1;
       pointer-events: all;
     }
   }
    &-show {
      position: relative;
      width: 25px;
      height: 5px;
      cursor: pointer;
      padding: 10px 17px;
      span {
        position: absolute;
        width: 5px;
        height: 5px;
        background: #FFFFFF;
        border-radius: 50%;
      }
      span:nth-child(1) {
        position: absolute;
        top: 50%;
        left: 6px;
        transform: translate(0, -50%);
      }
      span:nth-child(2) {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
      span:nth-child(3) {
        position: absolute;
        top: 50%;
        right: 6px;
        transform: translate(0, -50%);
      }
    }
    &-hidden {
      position: absolute;
      top: 100%;
      left: 0;
      width: 260px;
      opacity: 0;
      pointer-events: none;
      padding-top: 12px;
    }
  }
  &__hidden {
    &-inner {
      background: #242440;
      box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25);
      padding: 20px;
    }
    &-group {
      position: relative;
      &:not(:first-child) {
        margin-top: 45px;
        &:after {
          content: '';
          position: absolute;
          top: -23px;
          left: 0;
          width: 100%;
          height: 1px;
          background: #44446A;
        }
      }
    }
    &-subtitle {
      font-size: 12px;
      line-height: 120%;
      color: #9595AD;
    }
    &-link {
      display: block;
      font-size: 14px;
      line-height: 120%;
      color: #FFFFFF;
      margin-top: 15px;
    }
  }
  &__btn {
    width: 110px;
    height: 36px;
  }
  &__entrance {
    width: 90px;
    height: 36px;
    margin-left: 15px;
  }
  &__registration {
    width: 165px;
    height: 36px;
    margin-left: 15px;
  }
  &__notification {
    position: relative;
    margin-left: 35px;
    &-text {
      position: absolute;
      top: -5px;
      right: -10px;
      font-size: 10px;
      line-height: 120%;
      color: #FFFFFF;
      background: #DE6363;
      border-radius: 10px;
      padding: 2px;
    }
  }
  &__user {
    width: 36px;
    height: 36px;
    background: transparent;
    border-radius: 50%;
    margin-left: 35px;
    padding: 0;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__burger {
    position: relative;
    z-index: 6;
    width: 24px;
    height: 16px;
    cursor: pointer;
    display: none;
    span {
      position: absolute;
      left: 0;
      width: 100%;
      height: 2px;
      background: #ffffff;
      transition: all 0.5s;
      &:nth-child(1) {
        top: 0;
      }
      &:nth-child(2) {
        top: 50%;
        transform: translate(0, -50%);
      }
      &:nth-child(3) {
        bottom: 0;
      }
    }
    &--active {
      span {
        &:nth-child(1) {
          top: 7px;
          transform: rotate(-45deg);
        }
        &:nth-child(2) {
          display: none;
        }
        &:nth-child(3) {
          bottom: 7px;
          transform: rotate(45deg);
        }
      }
    }
  }
  &-user {
    &__menu {
      position: fixed;
      z-index: 2;
      top: 0;
      right: -105%;
      width: 150px;
      height: 100vh;
      background: #1F1F31;
      box-shadow: -10px 0px 20px rgba(0, 0, 0, 0.25);
      padding: 30px;
      transition: all 0.5s;
      overflow-y: auto;
      &::-webkit-scrollbar {
        width: 3px;
      }
      &::-webkit-scrollbar-track {
        background: #ffffff;
        border-radius: 3px;
      }
      &::-webkit-scrollbar-thumb {
        background: #1f1f31;
        border-radius: 3px;
      }
      &--active {
        right: 0;
      }
    }
    &__menu-bg {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.4);
      transition: all 0.8s;
      opacity: 0;
      pointer-events: none;
      &--active {
        opacity: 1;
        pointer-events: all;
      }
    }
    &__user {
      display: flex;
      flex-direction: column;
      align-items: center;
     i {
       width: 90px;
       height: 90px;
       border-radius: 50%;
       img {
         width: 100%;
         height: 100%;
       }
     }
      span {
        font-family: 'Neue Einstellung';
        font-size: 18px;
        line-height: 120%;
        font-weight: 700;
        color: #FFFFFF;
        margin-top: 10px;
      }
    }
    &__info {
      width: 100%;
      background: #45456D;
      border-radius: 15px;
      padding: 20px 25px;
      margin-top: 30px;
    }
    &__order {
      display: flex;
      flex-direction: column;
      align-items: center;
      &-icon {
        width: 40px;
        background: #7B47FE;
        border-radius: 5px 5px 10px 5px;
        display: flex;
        flex-direction: column;
        padding: 9px 4px;
      }
      &-icon span {
        height: 3px;
        background: #CBB7FF;
        border-radius: 3px;
        &:not(:first-child) {
          margin-top: 4px;
        }
        &:nth-child(1) {
          width: 100%;
        }
        &:nth-child(2) {
          width: 50%;
        }
        &:nth-child(3) {
          width: 24px;
        }
        &:nth-child(4) {
          width: 21px;
        }
      }
      &-title {
        font-size: 13px;
        line-height: 120%;
        color: #FFFFFF;
        margin-top: 8px;
      }
    }
    &__premium {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 30px;
      &-icon {
        width: 40px;
        img {
          width: 100%;
          height: auto;
        }
      }
      &-title {
        font-size: 13px;
        line-height: 120%;
        color: #FFFFFF;
        margin-top: 8px;
      }
    }
    &__help {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 30px;
      &-icon {
        width: 40px;
        img {
          width: 100%;
          height: auto;
        }
      }
      &-title {
        font-size: 13px;
        line-height: 120%;
        color: #FFFFFF;
        margin-top: 8px;
      }
    }
    &__settings {
      display: block;
      width: 34px;
      margin: 30px auto 0;
      img {
        width: 100%;
        height: auto;
      }
    }
    &__btn {
      width: 36px;
      height: 110px;
      margin: 20px auto 0;
      display: none;
      span {
        writing-mode: vertical-rl;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .header {
    &__burger {
      display: block;
    }
    &__right2 {
      position: absolute;
      z-index: 5;
      top: 0;
      left: -105%;
      width: 100%;
      height: 100vh;
      background: #242440;
      padding: 100px 30px 30px;
      flex-direction: column;
      transition: all 0.5s;
      &--active {
        left: 0;
      }
    }
    &__play {
      margin-left: 30px;
    }
    &__item-hidden {
      left: 50%;
      transform: translate(-50%, 0);
    }
    &__entrance {
      margin: 25px 0 0 0;
    }
    &__registration {
      margin: 25px 0 0 0;
    }
    &-user {
      &__menu {
        width: 90px;
        padding: 20px 15px;
      }
      &__user {
        i {
          width: 40px;
          height: 40px;
        }
        span {
          font-size: 14px;
        }
      }
      &__info {
        padding: 10px 5px;
        margin-top: 20px;
      }
      &__order {
        &-icon {
          width: 30px;
          span {
            height: 2px;
          }
        }
        &-title {
          font-size: 12px;
        }
      }
      &__premium {
        margin-top: 20px;
        &-icon {
          width: 30px;
        }
        &-title {
          font-size: 12px;
        }
      }
      &__help {
        margin-top: 20px;
        &-icon {
          width: 30px;
        }
        &-title {
          font-size: 12px;
        }
      }
      &__settings {
        width: 30px;
        margin-top: 20px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    &__logo {
      width: 80px;
    }
    &__play {
      font-size: 16px;
      margin-left: 20px;
    }
    &__item-hidden {
      left: unset;
      right: 0;
      transform: translate(0, 0);
      width: 200px;
    }
    &__item {
      margin-left: 20px;
    }
    &__btn2 {
      display: none;
    }
    &__notification {
      margin-left: 0;
      &-icon {
        display: block;
       svg {
         width: 18px;
         height: auto;
       }
      }
      &-text {
        top: -10px;
        right: -12px;
        font-size: 9px;
      }
    }
    &__user {
      margin-left: 20px;
      width: 25px;
      height: 25px;
    }
    &-user__btn {
      display: flex;
    }
  }
}