.catalog-main {
  padding: 45px 0 40px;
  &__title {
    font-family: 'Neue Einstellung';
    font-weight: 700;
    font-size: 48px;
    line-height: 120%;
    color: #FFFFFF;
  }
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 80px;
  }
  &__right {
    display: flex;
    align-items: center;
  }
  &__radio {
    display: flex;
    align-items: center;
  }
  &__gender {
    display: flex;
    align-items: center;
  }
  &__age {
    display: flex;
    align-items: center;
    margin-left: 60px;
  }
  &__subtitle {
    font-weight: 500;
    font-size: 16px;
    line-height: 120%;
    margin-right: 20px;
  }
  .radio-group {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    &:not(:last-child) {
      margin-right: 10px;
    }
    input {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      margin: 0;
      opacity: 0;
      cursor: pointer;
      &:checked~label {
        background: #6333B1;
      }
    }
    label {
      min-width: 80px;
      font-size: 16px;
      line-height: 120%;
      background: #323248;
      border-radius: 60px;
      padding: 7px 15px;
    }
  }
  &__select {
    height: 34px;
  }
}

@media screen and (max-width: 1200px) {
  .catalog-main {
    &__top {
      align-items: start;
    }
    &__radio {
      display: block;
    }
    &__age {
      margin: 25px 0 0 0;
    }
  }
}

@media screen and (max-width: 768px) {
  .catalog-main {
    &__title {
      font-size: 32px;
    }
    &__top {
      display: block;
    }
    &__right {
      justify-content: flex-end;
      margin: 25px 0 0 0;
    }
  }
}

@media screen and (max-width: 450px) {
  .catalog-main {
    &__top {
      margin-top: 45px;
    }
    &__right {
      display: block;
    }
    &__radio {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 15px;
      align-items: start;
    }
    &__gender {
      display: block;
    }
    &__age {
      display: block;
      margin: 0;
    }
    &__subtitle {
      margin-right: 0;
      margin-bottom: 20px;
    }
    .radio-group {
      justify-content: start;
      &:not(:last-child) {
        margin-right: 0;
        margin-bottom: 20px;
      }
    }
  }
}