.body-bg {
  position: relative;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 50%;
    height: 100%;
    background: #5C33CF;
  }
}
.authorization {
  position: relative;
  z-index: 1;
  padding: 20px 0 65px;
  .container {
    max-width: 1820px;
  }
  &__top {
    display: flex;
    justify-content: space-between;
  }
  &__logo {
    display: block;
    width: 110px;
    img {
      width: 100%;
      height: auto;
    }
  }
  &__close {
    width: 40px;
    height: 40px;
    background: #45456D;
    border-radius: 50%;
    padding: 0;
  }
  &__inner {
    display: flex;
    margin-top: 105px;
  }
  &__left {
    width: 50%;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 800px;
      height: 800px;
      background: radial-gradient(50% 50% at 50% 50%, rgba(254, 184, 255, 0.8) 0%, rgba(139, 165, 255, 0) 100%);
    }
    &-wrapp {
      position: relative;
      z-index: 1;
    }
  }
  &__right {
    width: 50%;
    max-width: 630px;
    margin: 0 auto;
  }
  &__title {
    font-family: 'Earths Mightiest';
    font-weight: 700;
    font-size: 90px;
    line-height: 120%;
  }
  &__right-title {
    font-family: 'Neue Einstellung';
    font-weight: 700;
    font-size: 40px;
    line-height: 120%;
  }
  &__tabs {
    margin-top: 55px;
    &-head {
      display: flex;
      align-items: center;
    }
    &-body {
      margin-top: 65px;
    }
    &-content {
      display: none;
      &--active {
        display: block;
      }
    }
  }
  &__tab-btn {
    font-family: 'Neue Einstellung';
    font-weight: 700;
    font-size: 24px;
    line-height: 120%;
    color: #FFFFFF;
    cursor: pointer;
    position: relative;
    &--active {
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: -5px;
        transform: translate(-50%, 0);
        width: 30px;
        height: 4px;
        background: #9039FF;
      }
    }
    &:not(:first-child) {
      margin-left: 50px;
    }
  }
  &__form {
    &-info {
      font-size: 16px;
      line-height: 120%;
      color: #AE7CFF;
    }
    &-btn {
      width: 160px;
      height: 50px;
      margin: 11px 0 0 auto;
    }
    &-btn2 {
      width: 200px;
      height: 50px;
      margin: 11px 0 0 auto;
    }
    &-radio {
      display: flex;
      align-items: center;
    }
    &-group {
      &:not(:first-child) {
        margin-top: 28px;
      }
      span {
        margin-bottom: 11px;
      }
      input {
        width: 100%;
        height: 50px;
      }
    }
  }
  .password-input {
    position: relative;
  }
  .password-btn {
    position: absolute;
    z-index: 1;
    top: 50%;
    right: 20px;
    transform: translate(0, -50%);
    width: 22px;
    cursor: pointer;
    img {
      width: 100%;
    }
  }
}