.btn-blue {
  font-size: 14px;
  line-height: 120%;
  color: #FFFFFF;
  background: #45456D;
  border: 1px solid transparent;
  border-radius: 30px;
  &:hover {
    background: transparent;
    border: 1px solid #45456D;
  }
}
.btn-violet {
  font-size: 14px;
  line-height: 120%;
  color: #FFFFFF;
  background: #6333B1;
  border: 1px solid transparent;
  border-radius: 50px;
  &:hover {
    background: transparent;
    border: 1px solid #6333B1;
  }
}
.btn-border {
  font-size: 18px;
  line-height: 120%;
  color: #AE7CFF;
  border: 1px solid #AE7CFF;
  border-radius: 50px;
  &:hover {
    background: #AE7CFF;
    color: #ffffff;
  }
}
.display3 {
  font-family: 'Neue Einstellung';
  font-size: 24px;
  line-height: 120%;
  font-weight: 700;
  color: #FFFFFF;
}
.display2 {
  font-family: 'Neue Einstellung';
  font-weight: 700;
  font-size: 32px;
  line-height: 120%;
  color: #FFFFFF;
}
.navigation {
  padding: 50px;
  &__list li {
    font-size: 24px;
    line-height: 120%;
    &:not(:first-child) {
      margin-top: 25px;
    }
  }
}
.nice-select {
  min-width: 160px;
  width: 100%;
  height: 34px;
  background: #323248 !important;
  border: unset;
  border-radius: 60px;
  display: flex;
  align-items: center;
  padding: 0 30px 0 12px;
  &:after {
    z-index: 1;
    top: 50%;
    right: 12px;
    transform: translate(0, -50%);
    width: 10px;
    height: 6px;
    background-image: url("../img/arrow-select.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
  .current {
    font-family: 'Rubik';
    font-weight: 400;
    font-size: 16px;
    line-height: 105%;
    color: #FFFFFF;
  }
  .list {
    border-radius: 0;
    border: unset;
    li {
      font-family: 'Rubik';
      font-weight: 400;
      font-size: 16px;
      line-height: 105%;
      background: #323248;
      border: unset;
      color: #FFFFFF;
      padding: 10px;
    }
  }
}
.nice-select .option:hover,
.nice-select .option.focus,
.nice-select .option.selected.focus {
  background: #242440;
}
.nice-select.open:after {
  transform: rotate(180deg) ;
}
.form {
  &-group {
    border: none;
    margin: 0;
    padding: 0;
    input {
      background: #323248;
      border-radius: 7px;
      font-size: 14px;
      line-height: 120%;
      border: transparent;
      color: #ffffff;
      padding: 0 20px;
      &::placeholder {
        font-size: 14px;
        line-height: 120%;
        color: #7D7D97;
      }
    }
    span {
      display: block;
      font-size: 16px;
      line-height: 120%;
    }
  }
  &-radio {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    &:not(:last-child) {
      margin-right: 10px;
    }
    .radio {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      margin: 0;
      opacity: 0;
      cursor: pointer;
      &:checked~label {
        background: #6333B1;
      }
    }
    label {
      min-width: 108px;
      font-size: 14px;
      line-height: 120%;
      background: #323248;
      border-radius: 60px;
      padding: 10px 15px;
    }
  }
}

@media screen and (max-width: 768px) {
  .display3 {
    font-size: 20px;
  }
  .display2 {
    font-size: 24px;
  }
}